@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0-beta3/css/all.min.css');

.alert {
    display: flex;
    align-items: center;
    border-radius: 5px;
    padding: 15px 10px;
    margin-bottom: 10px;
    /* max-width: 1100px; */
    width: 100%;
}

.alert.success {
    background-color: #c3e6cb;
    color: #155724;
    border: 1px solid #d4edda;
}

.alert.danger {
    background-color: #f8d7da;
    color: #721c24;
    border: 1px solid #f5c6cb;
}

.alert-icon {
    margin-right: 10px;
    font-size: 18px;
}

.alert-message {
    font-size: 15px;
}

.alert-icon i {
    vertical-align: middle;
}