* {
    box-sizing: border-box;
}

.skeleton {
    background-color: #bdb8b899;
    background-color: #cfcdcdad;
    margin: 6px 0px;
    box-shadow: 2px 2px 3px #b1aaaa;
}

/* for images of  book skeleton*/

.skeleton-wrapper-img {

    height: 267px;
    width: 163px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 12px;
    /* background-color: #e5e1e1; */

}

.skeleton-wrapper {
    position: relative;
    overflow: hidden;
    box-shadow: 3px 3px 4px #a3a2a2ab;
    border-radius: 10px;
    background: linear-gradient(90deg, #e8e8e8 0px, #f8f8f8 40px, #e8e8e8 80px);
    margin: 10px 0px 10px;
}

.skeleton-img {
    height: 100%;
    width: 100%;
}

.skeleton.type-image {
    width: 100%;
    height: 81%;
    border-radius: 10px;
    margin-bottom: 9px;
}

.skeleton.text {
    width: 100%;
    height: 12%;
    border-radius: 4px;
}

.skeleton.dr-text {
    width: 77%;
    height: 22px;
    border-radius: 8px;
    margin: 4px 0px;
}

.skeleton.ctg-img {
    width: 100%;
    height: 100%;
}

.skeleton.book-name {
    width: 82%;
    height: 37px
}

.skeleton.author-name {
    width: 45%;
    height: 25px;
}



/*for category images */
.skeleton-category-wrapper {
    width: 270px;
    height: 204px;
    border-radius: 53px;
    margin: 22px 12px;
}

.skeleton-category-img {
    height: 100%;
    width: 100%;
    position: relative;
}

.skeleton-category-img .skeleton-ctg-img {
    width: 100%;
    height: 100%;
}

.skeleton-category-text {
    background-color: #cfcdcdad;
    position: absolute;
    top: 64px;
    left: 0;
    width: 100%;
    height: 35px;
}

/* Download read section image view*/
.skeleton-wrapper-dr {
    height: 386px;
    width: 225px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 9px 0px;
    position: relative;
    overflow: hidden;
    margin-bottom: 25px;
    margin-right: 10px;
}

.skeleton-dr-text-boxes {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.skeleton-download-img {
    width: 165px;
    height: 247.5px;
}

.shimmer-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    animation: loading 2.2s infinite;
}

.shimmer {
    width: 50%;
    height: 100%;
    transform: skewY(-20deg);
    background-color: rgba(225, 225, 225, 0.2);
    box-shadow: rgba(225, 225, 225, 0.05);
}

/*download read book info section*/
.skeleton-wrapper-info-book {
    width: auto;
    height: 100px;
    padding: 8px 0px 0px 40px;
    position: relative;
    overflow: hidden;
}

.skeleton-info-book {
    width: 100%;
    height: 100%;

}

.skeleton-wrapper-book-expert {
    width: auto;
    height: 216px;
    overflow: hidden;
    position: relative;
    box-shadow: 2px 2px 3px #b1aaaa;
    border-radius: 17px;
    margin-left: 35px;
    margin-top: 17px;
}

.skeleton-book-expert {
    width: 100%;
    height: 100%;
    background-color: #ececec;
    margin: 6px 0px;
    border-radius: 17px;
}

@keyframes loading {
    0% {
        transform: translateX(-150%)
    }

    50% {
        transform: translateX(-60%)
    }

    100% {
        transform: translateX(150%);
    }


}

@media (max-width: 768px) {
    .skeleton-wrapper-img {
        width: 115px;
        height: 249px;
        padding: 11px 11px;
        margin: 6px 0px;
    }
}

@media (max-width:350px) {

    .skeleton-category-wrapper {
        width: 270px;
        height: 204px;
    }

}

@media (max-width:316px) {
    .skeleton-category-wrapper {
        width: 228px;
        height: 204px;
    }
}

@media (max-width: 280px) {

    .skeleton-wrapper-img {
        width: 104px;
        height: 179px;
        margin: 9px 0px;
    }

    .skeleton-category-wrapper {
        margin: 10px 0px;
        width: 192px;
        height: 159px;
    }

}