/* Reset default browser styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
}


.ad-nav {
    background-image: linear-gradient(to right, rgb(131, 28, 105), rgb(182, 7, 97));
    padding: 10px 34px;
    height: 11vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    opacity: .9;
}

.a-logo img {
    width: 31px;
    height: 24px;
    position: relative;
    right: 5px;
    top: 5px;
    display: inline-block;
}

.a-logo span {
    color: white;
    font-size: 21px;
}

.ad-nav ul {
    list-style-type: none;
    text-align: center;
}

.ad-nav li {
    display: inline-block;
    margin-right: 10px;
}

.ad-nav a {
    color: #fff;
    text-decoration: none;
    padding: 5px;
    font-size: 17px;
}

/* .ad-nav a:hover {
    background-color: #fff;
    color: rgb(182, 7, 97);
    border-radius: 3px;
} */

.ad-nav a {
    position: relative;
    color: #fff;
    text-decoration: none;
    margin: 0 10px;
    padding-bottom: 3px;
    /* Space for the line below */
}

.ad-nav a::after {
    content: '';
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 100%;
    height: 5px;
    background-color: rgb(206, 182, 195);

    transition: transform 0.3s ease;
    transform: scaleX(0);
    transform-origin: left;
}

.ad-nav a:hover::after {
    transform: scaleX(1);
}

.a-ul {
    display: inline-block;
}

.nav-button {
    display: inline-block;
    color: white;
}

.nav-button .bttn {
    background-color: white;
    width: 87px;
    font-weight: bold;
    border: 1.5px solid violet;
    padding: 6px;
    margin: 4px;
    color: #280324;
    margin-right: 7px;
}

.nav-button .bttn:hover {
    cursor: pointer;
    transform: scale(1.1);
    border: 1.5px solid rgb(66, 10, 66);
    background: #f2f2f2;
    box-shadow: 0px 0px 2px 2px rgb(207, 9, 111);
}

.ad-header {
    background-image: url('./bg3.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    height: 716px;
    position: relative;
}

.main-content {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 0px;
    top: 86px;
    height: 628px;

}

.admin-sub-container {
    position: relative;
    background: linear-gradient(rgba(176, 95, 58, 0.8), rgba(161, 108, 83, 0.8));
    padding: 70px 72px;
    color: white;
    border-radius: 26px;
    text-align: center;
    opacity: 0.9;
    border-image-slice: 100%;
    border: 2px solid rgba(128, 128, 128, 0.77);
    box-shadow: 0px 0px 3px 5px #9c512e;
}

.admin-sub-container h2 {
    font-size: 42px;
    margin-bottom: 13px;
}

.admin-sub-container p {
    font-size: 25px;
}

.a-button-cont {
    margin-top: 20px;
}

.bttn {
    background-color: transparent;
    border: 2px solid white;
    padding: 10px 20px;
    font-size: 15px;
    color: white;
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;

}



.bttn:hover {
    background-color: white;
    color: rgb(156, 79, 43);
}



footer {
    background-color: rgb(131, 28, 105);
    color: #fff;
    text-align: center;
    padding: 20px 10px;
}

.ad-footer-content {
    display: flex;
    justify-content: space-around;
    align-content: center;
    margin-top: 14px;
    font-size: 16px;
    color: #eb41bfe8;
}


.ad-div-line {
    width: 74vw;
    margin: 12px auto 0px auto;
}

.line-a {
    border-top: 2.5px solid #ad3c91e8;
}



.ad-footer-cntn p {
    display: inline;
}

.ad-footer-cntn img {
    width: 28px;
    height: 23px;
    position: relative;
    top: 5px;
    right: 10px;
}

.ad-footer-cntn {
    width: 429px;
}

.account-content-admin {
    display: none;
    width: auto;
    background-color: #f3f3f3;
    position: absolute;
    top: 37px;
    height: auto;
    padding: 12px 16px;
    box-sizing: border-box;
    box-shadow: 0px 0px 7px 3px #8c8c8c;
}

.logout-bttn:hover {
    color: rgb(68, 66, 66);
    transform: none;
}



.logout-bttn {
    color: grey;
    margin: 3px 18px;
    padding: 0px;
    font-size: 17px;
    font-weight: 600;
    background: none;
}