.sub-nav-region {
    height: 47px;
    background-color: #ffffff;
    box-shadow: -1px 0px 7px 2px #ae2573;
    padding: 10px 0px;
    text-align: center;

}

#flame-icon {
    color: #cc5f24;
    font-size: 17px;
}

#sub-nav-heading {
    color: #686868;
    margin-right: 30px;
}

.sub-nav-title,
#sub-nav-heading {
    display: inline-block;
}


.sub-nav-ul {
    display: inline-block;
}

.sub-nav-ul ul {
    list-style-type: none;

}

.sub-nav-ul ul li {
    display: inline-block;
    display: inline-block;
    margin: 0px 13px;
    font-size: 17px;

}

.sub-nav-ul ul li a {
    text-decoration: none;
    color: #706d6d;

}

.field-genre-main-container {
    width: 1170px;
    margin-top: 50px;
    margin-right: auto;
    margin-left: auto;
    padding-left: 15px;
    padding-right: 15px;
    font-size: 14px;
    line-height: 1.42857;
    height: auto;
}

.sub-main-container {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.view-form-search-result {
    width: 33.33333%;
    background-color: #fbfbfb;
    height: 100%;
    padding: 35px 30px;

}

.genre-item-books {
    width: 69.66667%;
    background-color: #ffffff;
    height: 100%;

}

.inner-view-search-heading {
    color: #992d75;
    font-size: 22px;
    margin-bottom: 20px;
}

.stars-rating-divs {
    margin: 12px 0px;
}

.stars {
    display: inline-block;
    margin-left: 15px;
    font-size: 19.5px;
}

.star-checkbox-input input {
    height: 15px;
    width: 15px;
    display: inline;
}

.stars .checked {
    color: #992d75
}

.stars .unchecked {
    color: #dadada;
}


.language-search {
    margin: 32px 0px;
}

.language-drop-down {
    margin-bottom: 40px;
}

.custom-dropdown {
    position: relative;
    width: 280px;
    width: 91%;
}

.selected-option:hover {
    box-shadow: 0px 0px 3px 1.5px skyblue;
}

.selected-option {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 7.5px;
    cursor: pointer;
    width: 100%;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    font-size: 15px;
}

.custom-dropdown select {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-color: transparent;
    border: 1px solid #ccc;
    padding: 10px;
    width: 100%;
    font-size: 14px;
    cursor: pointer;
}

.custom-dropdown select::-ms-expand {
    display: none;
}

.dropdown-arrow {
    position: absolute;
    top: 33%;
    right: 17px;
    cursor: pointer;
}

.custom-dropdown select::-ms-expand {
    display: none;
}

.custom-dropdown select option {
    background-color: #fff;
}

.custom-dropdown select option:hover {
    background-color: #f5f5f5;
}

/* genre-item-contianer*/

.form-items {
    width: 299px;
    padding: 7px;

}

.checker {
    display: inline-block;
    text-transform: capitalize;
    font-size: 18px;
    color: #616161;
    padding: 0;
    margin-right: 10px;
    font-weight: 400;

}

.form-checkbox {
    width: 15px;
    height: 15px;
}

.gernre-item-books-main-cont {
    padding: 0px 6px;
}

.form-checkboxes-button {
    margin: 30px 0px;
    text-align: center;
}

.apply-button {
    background-color: #831c69;
    width: 167px;
    font-weight: 600;
    border: 1.5px solid #f7d8f7;
    padding: 7px;
    margin: 4px;
    margin-right: 4px;
    color: white;
    margin-right: 7px;
    font-size: 17px;
}

.apply-button:hover {
    cursor: pointer;
    transform: scale(1.02);
}

.view-filter {
    display: none;
}

@media (max-width: 1218px) {
    .sub-main-container {
        width: 100%;
        justify-content: center;
    }

    .field-genre-main-container {
        width: 990px;

    }

    .genre-item-books {
        width: 58.667%;
    }

    .view-form-search-result {
        width: 32.333%;
    }
}

@media (max-width: 1084px) {
    .sub-main-container {
        width: 85vw;
    }

    .field-genre-main-container {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .genre-item-books {
        width: 68.667%;
    }

    .cover {
        margin: 22px 12px;
        width: 128px;
        height: 231px;
    }

    .sub-nav-ul ul li {
        margin: 0px 5px;
        font-size: 15px;
    }

    #sub-nav-heading {
        color: #686868;
        margin-right: 5px;
    }

}

@media (max-width: 760px) {
    .sub-main-container {
        flex-wrap: wrap;
        width: 100%;
    }

    .view-filter {
        display: block;
    }

    .view-filter .filter {
        /* border: 2px solid gray; */
        width: 100%;
        padding: 6px 0px;
        text-align: center;
        font-size: 23px;
        font-weight: 600;
        color: #9e2b74;
        display: block;
        width: 100%;
        margin-bottom: 19px;
        box-shadow: -1px 0px 4px 1px #ae2573;
    }

    .sub-nav-region {

        height: 47px;

        width: 100%;
        display: none;
    }

    .field-genre-main-container {
        padding-bottom: 32px;

        width: 100%;
        margin: 10px 0px;
    }

    .view-form-search-result {
        width: 100%;
    }

    .genre-item-books {
        padding-bottom: 40px;
        width: 100%;
    }

    .cover {
        margin: 22px 12px;
        width: 107px;
        height: 187px;
    }

    .inner-view-search {
        display: none;
    }

    .block {
        display: block;
    }
}

@media (max-width: 760px) {
    .sub-main-container {
        flex-wrap: wrap;
    }

    .view-filter {
        display: block;
    }

    .view-filter .filter {
        /* Other styles here */
        width: 100%;
    }

    .sub-nav-region {
        height: 47px;
        width: 100%;
        display: none;
    }

    .field-genre-main-container {
        padding-bottom: 32px;
        width: 100%;
        margin: 10px 0px;
    }

    .view-form-search-result {
        width: 100%;
    }

    .genre-item-books {
        padding-bottom: 40px;
        width: 100%;
    }

    .cover {
        margin: 22px 12px;
        width: 107px;
