    }
}

@media (max-width: 280px) {
    .view-form-search-result {
        width: 100%;
        padding: 25px 30px 10px;
    }

    .genre-item-books {
        width: 100%;
    }

    .cover {
        margin: 12px 12px;
        width: 104px;
        height: 179px;
    }

    .book_name {
        font-size: 12px;
        text-decoration: none;
        font-weight: bold;
        color: grey;
    }
}