.ua-main-container {
    height: 500px;
    width: 100%;
}


.ua-sub-container-a {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

}

.user-logo-letter {
    width: 110px;
    height: 110px;
    background-color: #9e2b74;
    margin-top: 42px;
    margin-bottom: 20px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}


#name-letter {
    font-size: 64px;
}



.w-name {
    line-height: 2.25rem;
    font-size: 1.75rem;
    letter-spacing: 0;
    font-weight: 400;
    hyphens: auto;
    word-break: break-word;
    word-wrap: break-word;
    color: rgb(32, 33, 36);
    text-align: center;
}

#manage-info p {
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 1px;
    line-height: 24px;
    word-break: break-word;
    word-wrap: break-word;
    color: rgb(95, 99, 104);
    margin-top: 16px;
    text-align: center;
}

.ua-sub-container-b {
    width: 99vw;
    height: auto
}

.basic-info-container {
    margin: 12px auto 30px;
    width: 70vw;
    height: 100%;
}



/* Styling for the basic account information section */
.basic-info {
    padding: 20px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.name_clss {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    position: relative;
    left: 30px;
}

input[type="text"] {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 315px;
}

.edit-icon {
    margin-left: 15px;
}

.edit-icon span {
    color: #676363;
    cursor: pointer;

}

.edit-icon span i {
    font-size: 21px;
}

.edit-icon span i:hover {
    color: black;
}

.save-reset-button {
    display: flex;
    justify-content: flex-end;
    width: 423px;
    margin-top: 10px;
}

button {
    padding: 10px;
    border: none;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    font-weight: bold;
    margin: 0px 6px;
    background-color: rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s ease;
}


#reset-button {
    background-color: #d9534f;
}

#save-button {
    background-color: #5cb85c;
}

#reset-button:hover {
    background-color: #d7342f;
}

#save-button:hover {
    background-color: #30af30;
}

.line2 {
    width: 80%;
    border-bottom: 2px solid black;
    height: 2px;
    display: inline-block;
}

.line-div2 {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.download-table {
    border-collapse: collapse;
    margin: 20px auto;
    width: 90%;
}

.download-table thead {
    background-color: #1b1c51 !important;
    color: #fff;
}

.download-table th,
.download-table td {
    padding: 10px;
    border: 1px solid #8f8c8c;
}

.download-table th {
    text-align: left;
}

.download-books {
    margin: 12px 0px;
}

.download-books h1 {
    text-align: center;
    color: #1b1c51;

}

.modal-1 {
    display: none;
    position: fixed;
    z-index: 9999;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
    background-color: rgba(255, 255, 255, 0.9);
    margin: 17% auto;
    padding: 13px 20px;
    border-radius: 8px;
    max-width: 500px;
    box-shadow: 0 0 2px 3px rgba(125, 0, 88, 0.2);
}

.close {
    color: rgb(136, 12, 105);
    float: right;
    font-size: 28px;
    font-weight: bold;
    cursor: pointer;
}

.close:hover,
.close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}

.modal-content h2 {
    margin-bottom: 17px;
    color: #444;
}

.form-item-l {
    width: 100%;
    text-align: center;
    margin-bottom: 15px;
}

.form-item-l input {
    width: 100%;
    border: 1px solid #9e2b74;
    padding: 9px 15px;
}

.form-item-l input:focus {

    outline: none;
    box-shadow: 0px 0px 1.3px 1px #9e2b74;
}

#reset-bttn {
    background-color: #9e2b74;
    width: 114px;
    font-weight: bold;
    border: 1.5px solid violet;
    padding: 10px 0px;
    margin: 13px 0px;
    margin-right: 7px;
    color: white;
}

#reset-bttn:hover {
    cursor: pointer;
    transform: scale(1);
    border: 1.5px solid rgb(85, 48, 85);
    box-shadow: 0px 0px 2px 2px rgb(243, 133, 189);
}

.no-downloads {
    font-size: 19px;
    color: #535151;
    text-align: center;
}

@media (max-width: 760px) {
    .modal-content {
        background-color: rgba(255, 255, 255, 0.9);
        margin: 17% auto;
        padding: 13px 20px;
        border-radius: 8px;
        max-width: 400px;
    }
}

@media (max-width: 530px) {
    .modal-content {
        background-color: rgba(255, 255, 255, 0.9);
        margin: 17% auto;
        padding: 13px 20px;
        border-radius: 8px;
        max-width: 300px;
    }
}

@media (max-width: 400px) {
    .modal-content {
        background-color: rgba(255, 255, 255, 0.9);
        margin: 17% auto;
        padding: 13px 20px;
        border-radius: 8px;
        max-width: 200px;
    }

    .name_clss {
        left: 2px;
        width: 100%;
    }

    .save-reset-button {
        width: 100%;
    }

    .basic-info {
        width: 100%;
    }

    .basic-info-container {
        margin: 12px auto 30px;
        width: 80vw;
        height: 100%;
    }

    input[type="text"] {
        padding: 10px;
        border: 1px solid #ccc;
        border-radius: 4px;
        width: 90%;
    }

    .user-logo-letter {
        width: 100px;
        height: 100px;
        margin-top: 20px;
        margin-bottom: 15px;
        border-radius: 50%;
    }
}