/* owl-carousel */
.editor_books,
.card {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 16%;
    position: relative;
}

.editor-book-cover {
    margin: 0px 0px;
    width: 100%;
    height: 260px;

}

.wrapper {
    max-width: 1217px;
    width: 100%;
    position: relative;
}

.wrapper i {
    top: 46%;
    height: 50px;
    width: 50px;
    cursor: pointer;
    font-size: 1.25rem;
    position: absolute;
    text-align: center;
    line-height: 50px;
    border-radius: 50%;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.23);
    transform: translateY(-50%);
    transition: transform 0.1s linear;
    z-index: 10;
    background: white;
}

.wrapper i:active {
    transform: translateY(-50%) scale(0.85);
}

.wrapper i:first-child {
    left: -22px;
    font-size: 29px;
}

.wrapper i:last-child {
    right: -22px;
    font-size: 29px;
}

.wrapper .carousel {
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: calc((100% / 6) - 12px);
    /* overflow-x: auto; */
    scroll-snap-type: x mandatory;
    gap: 16px;
    border-radius: 8px;
    scroll-behavior: smooth;
    scrollbar-width: none;
}

.carousel .card {
    scroll-snap-align: start;
    height: 342px;
    list-style: none;
    cursor: pointer;
    padding-bottom: 15px;
    flex-direction: column;
    border-radius: 8px;
    justify-content: flex-start;
}

.carousel::-webkit-scrollbar {
    display: none;
}

.carousel.no-transition {
    scroll-behavior: auto;
}

.carousel.dragging {
    scroll-snap-type: none;
    scroll-behavior: auto;
}

.carousel.dragging .card {
    cursor: grab;
    user-select: none;
}

.carousel :where(.card, .img) {
    display: flex;
    justify-content: center;
    align-items: center;
}

.carousel .card .img {
    width: 181px;
    height: 268px;
}

.card .img img {
    width: 173px;
    height: 260px;
}

.carousel .card p {
    font-weight: 500;
    font-size: 16px;
    margin: 10px 0px 5px 11px;
}

.Eb-outer-contianer {
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.disable-scroll {
    overflow-x: hidden;
}

.disabled {
    display: none;
}

