:root {
    --primary-color: rgb(162, 42, 116);
}

.ad-add-book-sec {
    height: auto;
    width: 100%;
}

.ad-add-book-container {
    width: 85%;
    margin: 12px auto;
}

.ad-heading {
    color: var(--primary-color);
    font-size: 29px;
    margin: 23px 0px;
    text-align: center;
}

.ad-add-sub-cont {
    /* max-width: 757px; */
    width: 85%;
    margin: 0 auto;
    padding: 27px 42px;
}

.form-group {
    margin-bottom: 35px;
}

.form-group label {
    display: block;
    margin-bottom: 10px;
    font-weight: 600;
    color: #585252;
    font-size: 19px;
}

.form-group input[type="text"],
.form-group input[type="number"],
.form-group textarea {
    width: 100%;
    padding: 8px;
    border: 1px solid #979191;
    border-radius: 5px;
    font-size: 14px;
    box-shadow: 0px 0px 1px 2px #EAE2E2;
}

.form-group input:focus,
textarea:focus {
    box-shadow: 0px 0px 1px 2px #EAE2E2;
    border: none;
}

.form-group textarea {
    resize: vertical;
    height: 100px;
}

button[type="submit"] {
    padding: 11px 43px;
    background-color: var(--primary-color);
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 550;
}

button[type="submit"]:hover {
    background-color: rgba(162, 42, 116, 0.8);
}

.form-group input[type="checkbox"] {
    width: 20px;
    height: 20px;
    margin-right: 10px;
    position: relative;
    left: 800px;
    top: 2px;
}

.checkbox {
    display: flex;

}