body {
    font-family: Arial, sans-serif;
    background-color: #f1f1f1;
    margin: 0;
    padding: 0;
}



.user-list {
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    height: auto;
    border-radius: 5px;
    max-width: 1100px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin: 10px auto 99px;
}

.user-Downloads {
    max-width: 1100px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    height: auto;
    border-radius: 5px;
    margin: 5px auto 50px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

h1 {
    text-align: center;
    color: rgb(131, 28, 105);
    margin-bottom: 20px;
}

table {
    width: 100%;
    border-collapse: collapse;
}

th,
td {
    padding: 10px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}

thead th {
    background-color: rgba(200, 200, 200);
    color: #4d4a4a;
    font-weight: 600;
    padding: 10px;
    position: relative;
}

tbody tr:hover {
    background-color: rgba(131, 28, 105, 0.1);
}

td:nth-child(4),
td:nth-child(5),
td:nth-child(6) {
    font-style: italic;
    color: #777;
}

td:nth-child(4) {
    font-weight: bold;
}

td:nth-child(5),
td:nth-child(6) {
    text-align: center;
}