:root {
    --primary-color: rgb(162, 42, 116);
}

.ad-allcategories-sec {
    width: 100%;
    height: auto;
    min-height: 800px;
    max-height: auto;
}

.ad-allcategories-sub-sec {
    width: 85%;
    height: auto;
    margin: 10px auto;
}

.ad-heading {
    color: var(--primary-color);
    font-size: 29px;
    margin: 23px 0px;
}

.ad-category-sub-cont {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.ad-category-card {
    height: auto;
    width: 270px;
    background-color: rgb(255, 255, 255);
    padding: 10px;
    box-shadow: 0px 0px 7px 2px rgb(195, 189, 192);
    margin: 15px 0px 25px;
}

.ad-category-card img {
    height: 171px;
    width: 100%;
}

.ad-category-name {
    font-size: 18px;
    font-weight: 650;
    margin: 10px 0px 13px;
    text-align: center;
    color: #645d5d;
}

.ad-category-icon {
    text-align: center;
}

.ad-edit-button,
.ad-delete-button {
    padding: 7px 15px;
    background-color: var(--primary-color);
    color: white;
    font-size: 12px;
    border-radius: 23px;
    cursor: pointer;
}

.ad-edit-button {
    margin-right: 3px;
}

.ad-delete-button {
    margin-left: 3px;
}

.ad-edit-button i,
.ad-delete-button i {
    font-size: 14px;
}

/* Category Edit Modal Styles */
.category-edit-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
    align-items: center;
    justify-content: center;
}

.category-edit-modal {
    background-color: #fff;
    padding: 40px 35px 30px 35px;
    border-radius: 5px;
    max-width: 400px;
    text-align: center;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
    position: relative;
}

.category-edit-modal h2 {
    color: var(--primary-color);
    margin-bottom: 10px;
}

.category-edit-modal form label {
    display: block;
    margin-bottom: 5px;
}

.category-edit-modal form input[type="text"] {
    width: 100%;
    padding: 5px;
    margin-bottom: 10px;
    border: 1px solid #5e5959;
    border-radius: 3px;
}

.category-edit-modal form button[type="submit"] {
    padding: 10px 20px;
    background-color: var(--primary-color);
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.category-edit-modal form button[type="submit"]:hover {
    background-color: rgba(162, 42, 116, 0.8);
}

.category-edit-modal .close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px;
    cursor: pointer;
}

.category-edit-modal .close-modal {
    top: 1px;
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px;
    cursor: pointer;
    color: var(--primary-color);
}