:root {
    --primary-color: rgb(162, 42, 116);
}

.ad-heading {
    color: var(--primary-color);
    font-size: 29px;
    margin: 23px 0px;
}

.ad-add-category-sec {
    width: 100%;
    height: 800px;
}

.ad-add-category-sub-sec {
    width: 85%;
    margin: 50px auto;
}

.ad-add-category-contianer {
    width: 100%;
}

.ad-add-category-container {
    background-color: #f8f8f8;
    border-radius: 5px;
    padding: 20px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
    max-width: 400px;
    margin: 0 auto;
    margin-top: 20px;
}

.add-category-form {
    margin: 0 auto;
}

.form-group {
    margin-bottom: 15px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: 600;
    color: #555;
}

.form-group input[type="text"] {
    width: 100%;
    padding: 8px;
    border: 1px solid #777171;
    border-radius: 5px;
    font-size: 14px;
}

.form-group button[type="submit"] {
    padding: 10px 20px;
    background-color: var(--primary-color);
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.form-group button[type="submit"]:hover {
    background-color: rgba(162, 42, 116, 0.8);
}

.add-category-bttn {
    text-align: center;
    margin: 66px 0px;
}

.add-category-bttn button {
    padding: 10px 20px;
    background-color: var(--primary-color);
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.add-category-bttn button:hover {
    background-color: rgba(162, 42, 116, 0.8);
}