   * {
       margin: 0;
       padding: 0;
       box-sizing: border-box;

   }

   .login-main-container {
       width: 100%;
       height: 100vh;
       display: flex;
       justify-content: center;
       align-items: center;

   }

   .login-sub-contianer {
       background-color: #ffffff;
       width: auto;
       height: auto;
       border-radius: 3px;
       border: 2px solid black;
       padding: 12px 72px 34px;
       box-shadow: 0 0 0 8px rgba(64, 61, 61, .6);
       box-shadow: 0 0 0 7px rgba(126, 121, 121, 0.6);
   }

   .my-logo-div {
       width: 100%;
       display: flex;
       justify-content: center;

   }

   .my-ebooks {
       text-align: center;
       margin-top: 30px;
       background-image: linear-gradient(to right, rgb(144, 32, 116), rgb(219, 50, 136));
       width: 64px;
       height: 64px;
       display: flex;
       align-items: center;
       justify-content: center;
       border-radius: 50%;

   }

   .my-ebooks img {
       width: 40px;
       height: 36px;
       transform: rotateX(23deg);
   }

   .login-form {
       display: flex;
       flex-direction: column;
       justify-content: center;
       align-items: center;
       width: 100%;
       margin-top: 24px;
   }




   .login-title {
       color: #555;
       font-size: 28px;
       margin-bottom: 4px;
       font-weight: 500;
   }


   .login-description {
       margin-bottom: 34px;
       font-size: 17px;
   }

   .form-item-l {
       width: 100%;
       text-align: center;
       margin-bottom: 15px;
   }

   .form-item-l input {
       width: 100%;
       border: 1px solid #9e2b74;
       padding: 9px 15px;
   }

   .form-item-l input:focus {

       outline: none;
       box-shadow: 0px 0px 1.3px 1px #9e2b74;
       ;

   }

   .submit-bttn {
       background-color: #9e2b74;
       width: 114px;
       font-weight: bold;
       border: 1.5px solid violet;
       padding: 10px 0px;
       margin: 12px 0px;
       margin-right: 7px;
       color: white;
       width: 119px;
   }

   .submit-bttn:hover {
       cursor: pointer;
       transform: scale(1);
       border: 1.5px solid rgb(85, 48, 85);
       box-shadow: 0px 0px 1px 3px rgb(243, 133, 189);
   }

   @media screen and (max-width: 600px) {
       .login-sub-contianer {
           padding: 5px 27px 10px;
       }

       .login-description {
           font-size: 14px;
       }

       .my-ebooks {
           width: 61px;
           height: 60px;
       }

       .my-ebooks img {
           width: 37px;
           height: 32px;
       }

       .submit-bttn {
           margin: 16px 0px;
       }

   }

   @media screen and (max-width:390px) {
       .login-sub-contianer {
           width: 250px;
       }

       .login-title {
           font-size: 22px;
           margin-bottom: 4px;
           font-weight: 500;
       }

       .my-ebooks img {
           width: 32px;
           height: 29px;
       }

       .my-ebooks {
           width: 51px;
           height: 50px;
       }

       .submit-bttn {
           margin: 13px 0px;
       }
   }