:root {
    --primary-color: rgb(162, 42, 116);
}

.all-book-sec {

    width: 100%;
    min-height: 800px;
    max-height: auto;
}

.all-book-contianer {
    width: 87%;
    margin: auto;
    padding: 20px;
}

.ad-heading {
    color: var(--primary-color);
    font-size: 29px;
    margin: 23px 0px;
}

.ad-sub-contianer {
    display: flex;
    justify-content: space-around;
    align-self: center;
    flex-wrap: wrap;
}

.ad-book-card {
    /* border: 2px solid var(--primary-color); */
    height: auto;
    width: 195px;
    padding: 10px;
    background-color: #fff;
    /* box-shadow: 0px 0px 7px 2px rgb(231, 153, 201); */
    box-shadow: 0px 0px 7px 2px rgb(195, 189, 192);
    margin: 12px 23px 24px 0px;
}

.ad-book-card img {
    height: 217px;
    width: 100%;
}

.ad-book-card .ad-book-name {
    margin: 4px 0px 2px;
    font-size: 18px;
    font-weight: 600;
    color: #605d5d;
}

.ad-book-card .ad-author-name {
    font-size: 14px;
}

.ad-free-price {
    display: flex;
    justify-content: flex-start;
    margin: 3px 0px 10px;
}

.ad-free-price .ad-free {
    margin: 0px 42px 0px 0px;
    margin: 0px 42px 0px 0px;
    color: #0ca60c;
}

.ad-free-price .ad-price {
    font-weight: 607;
}

.ad-icons {
    text-align: center;
}

.ad-edit-button,
.ad-delete-button {
    padding: 7px 15px;
    background-color: var(--primary-color);
    color: white;
    font-size: 12px;
    border-radius: 23px;
    cursor: pointer;
}

.ad-edit-button {
    margin-right: 3px;
}

.ad-delete-button {
    margin-left: 3px;
}

.ad-edit-button i,
.ad-delete-button i {
    font-size: 14px;
}

/* Modal Styles */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
    align-items: center;
    justify-content: center;
}

.open-modal {
    padding: 10px 20px;
    background-color: #a22a74;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.modal h2 {
    margin-bottom: 20px;
    color: #a22a74;
}

.modal form label {
    display: block;
    font-size: 18px;
    margin-bottom: 6px;
    color: #3a3838;
}

.modal form input[type="text"],
.modal form input[type="number"],
.modal form textarea {
    width: 100%;
    padding: 8px 6px;
    margin-bottom: 10px;
    border: 1px solid #979191;
    font-size: 16px;
    border-radius: 5px;
}

.modal form textarea {
    height: 100px;
}

.modal form button[type="submit"] {
    padding: 10px 20px;
    background-color: #a22a74;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.modal form button[type="submit"]:hover {
    background-color: #851e5a;
}

.ad-form-items {
    margin: 12px 0px;
}

.ad-form-items input[type="checkbox"] {
    width: 20px;
    height: 20px;
    margin-right: 10px;
    position: relative;
    left: 591px;
    top: 2px;
}

.checkbox {
    display: flex;

}

.modal {
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
    max-width: 800px;
    width: 90%;
    position: relative;
    overflow-y: auto;
    max-height: 89vh;
    display: block;
}

.modal form {
    margin-top: 20px;
}

.close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 30px;
    cursor: pointer;
    color: #333;
}

.close-modal:hover {
    color: #a22a74;
}

.delete-modal-container {
    display: flex;

}

.delete-modal-overlay {

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;

}

.close-modal-delete {
    position: absolute;
    top: 5px;
    right: 5px;
    font-size: 30px;
    cursor: pointer;
    color: #333;
}


.close-modal-delete:hover {
    color: #a22a74;
}

.delete-modal {
    background-color: #fff;
    border: 2px solid var(--primary-color);
    padding: 30px 20px 35px;
    height: auto;
    border-radius: 5px;
    max-width: 400px;
    text-align: center;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
    /* Adding box-shadow */
}

.delete-modal h2 {
    color: var(--primary-color);
    margin-bottom: 10px;
}

.delete-modal p {
    color: rgb(141, 141, 141);
    margin-bottom: 20px;
}

.modal-buttons {
    display: flex;
    justify-content: center;
}

.modal-buttons button {
    padding: 8px 16px;
    margin: 0 10px;
    background-color: var(--primary-color);
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    border: 1px solid rgb(162, 162, 162)
}

.modal-buttons button:hover {
    background-color: rgba(255, 255, 255, 0.8);
    color: #851e5a;
    box-shadow: 0px 0px 3px 1px var(--primary-color);
}