@import url('https://fonts.googleapis.com/css2?family=Lato&family=Libre+Franklin:wght@200&family=Open+Sans&family=Tajawal:wght@300&family=Ubuntu:wght@300&display=swap');



* {
    padding: 0;
    margin: 0;
    list-style: none;
    box-sizing: border-box;
    font-family: 'Ubuntu', sans-serif;
    font-family: 'Tajawal', sans-serif;
    font-family: 'Open Sans', sans-serif;
    font-family: 'Libre Franklin', sans-serif;
    font-family: 'Lato', sans-serif;

}

.nav-bg-image {
    background-image: url('https://images.pexels.com/photos/3704611/pexels-photo-3704611.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    height: 567px;
    /* position: relative; */
}

.nav {

    display: flex;
    justify-content: space-around;
    background-color: rgb(136, 12, 105);
    background-image: linear-gradient(to right, rgb(131, 28, 105), rgb(182, 7, 97));
    align-items: center;
    height: 11.5vh;
    width: 100%;
    opacity: .9;
}

.logo-discover #main-logo img {
    width: 31px;
    height: 24px;
    right: 5px;
    position: relative;
    top: 5px;
    cursor: pointer;
}


.block-header {
    display: inline-block;
    font-size: 20px;
    color: white;
    font-weight: bold;
    position: relative;
}

#down-arrow {
    color: white;
    position: relative;
    top: 2px;
    left: 5px;

}

.rotated {
    transform: rotate(180deg);
}


.block-content {
    /* display: none; */
    width: 433px;
    background-color: #f3f3f3;
    position: absolute;
    top: 37px;
    height: auto;
    padding: 28px;
    box-sizing: border-box;
    box-shadow: 0px 0px 7px 3px #8c8c8c;
    padding-bottom: 37px;
}

.genres-list h3 {
    line-height: 1.1;
    margin-bottom: 12px;
    color: #89276b;
}

.genres-list ul {
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;

}



.genres-list ul li a {
    text-decoration: none !important;
    color: #534e4e;
}

.nav .login-signup-bttns .bttn {
    background-color: white;
    width: 87px;
    font-weight: bold;
    border: 1.5px solid violet;
    padding: 6px;
    margin: 4px;
    color: #280324;
    margin-right: 7px;
}

.nav .login-signup-bttns .bttn:hover {
    cursor: pointer;
    transform: scale(1.1);
    border: 1.5px solid rgb(66, 10, 66);
    background: #f2f2f2;
    box-shadow: 0px 0px 2px 2px rgb(207, 9, 111);
}

.nav .search-box input {
    width: 259px;
    line-height: 1.5;
    border: 2px solid #881761;
    height: 26px;
    padding: 16px 9px;
    font-size: 15px;
    border-radius: 20px;
}

.search-box input:focus {
    outline: 1px solid white;
}

#logo-search {
    position: relative;
    top: 1px;

    right: 42px;
}

.nav .search-box i {
    font-size: 18px;
    color: #9a145f;
    cursor: pointer;

}

.search-box input[type="text"]::placeholder {
    color: #3a3939;
}

.nav .search-box i:hover {
    color: #ca639c;
}

.account-info {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

.account-user {
    margin-left: 12px;
    position: relative;
}

#down-arrow-user {
    color: #ffffff;
    font-size: 18px;
    position: relative;
    left: 3px;
    top: 2px;
}

.rotatedUser {
    transform: rotate(180deg);
}

.user-logo-i {
    font-size: 19px;
    position: relative;
    right: 3px;
}

#user {
    margin: 0px 3px;
    font-size: 20px;
    color: white;
    font-weight: bold;
}

.account-content {
    display: none;
    width: auto;
    background-color: #f3f3f3;
    position: absolute;
    top: 37px;
    height: auto;
    padding: 12px 16px;
    box-sizing: border-box;
    box-shadow: 0px 0px 7px 3px #8c8c8c;
}

.account-items ul {
    list-style: none;
}

.account-items ul li {
    margin-bottom: 6px;
}

.account-items ul li a {
    text-decoration: none !important;
    color: #534e4e;
}



.container {
    display: flex;
    justify-content: center;
    height: 450px;
    align-items: center;
}

.sub_container {
    width: 100%;
    text-align: center;
    color: #881761;
}

.container .sub_container p {
    font-size: 26px;
}

.container .sub_container h2 {
    font-size: 47px;
}

.section_a {
    width: 100%;
    height: auto;
}

.container_a {
    height: auto;
    width: 80vw;
    margin: 12px auto 76px auto;

}

.item img {
    width: 100%;
    height: 100%;
    border-radius: 52px;
    box-shadow: 4px 5px 5px #6d6a6aad
}

.genre_items {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.genre_items .item {
    margin: 22px 12px;
    width: 270px;
    height: 204px;
    position: relative;
}

.container_a .genre_heading {
    font-size: 23px;
    color: #881761;
    margin: 30px 0px 22px 41px;
    font-weight: 700;
    font-size: 22px;
    text-transform: uppercase;
}

.genre_items .item .text {
    position: absolute;
    top: 70px;
    left: 0px;
    padding: 6px 0px;
    font-weight: bold;
    width: 100%;
    text-align: center;
    background: #585757;
    opacity: 0.65;
    color: white;
    font-size: 19px;
}

.editor-choice-contianer {
    width: 100%;
    height: auto;
}

.sub-contianer-editor-choice {
    height: auto;
    width: 80vw;
    margin: 12px auto 40px auto;
}

.section_b {
    width: 100%;
    height: auto;

}

.container_b {
    height: auto;
    width: 80vw;
    margin: 12px auto 50px auto;
}

.free_books {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.books {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 16%;
    position: relative;
}

.books-cls {
    display: flex;
    background: #f7f7f7;
    text-transform: capitalize;
    justify-content: center;
    align-items: center;
    flex-basis: 16%;
    margin: 5px 0px;
    border-radius: 7px;
}

.free_books .cover {
    margin: 22px 12px;
    width: 163px;
    height: 323px;

}

.cover {
    margin: 22px 9px 12px;
    height: 267px;
    width: 163px;

}

.book_hover {
    width: 326px;
    height: 233px;
    display: flex;
    border: 1px solid #f3f3f3;
    display: none;
    position: absolute;
    background-color: #fffdfd;
    z-index: 1;
    cursor: pointer;
    box-shadow: 0px 0px 4px 3px #e2dbdb;
    top: 4px;

}

.book_hover .image {
    width: 50%;
    height: 100%;
}

.book_hover .image img {
    width: 100%;
    height: 100%;
}

#read_more_ {
    width: 110px;
}

.hover_bttn {

    text-align: center;
    align-self: center;
    font-size: 17px;
    padding: 2px 4px;
    background-color: purple;
    border: 2px solid #866086;
    color: white;
    border-radius: 2px;
}

.book_hover .hover_content {
    width: 50%;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: center;


}

.container_b .cover img {
    width: 100%;
    height: 72%;
    cursor: pointer;
}

.cover img {
    width: 100%;
    height: 89%;
    cursor: pointer;
}

.book_name {
    font-size: 16px;
    text-decoration: none;
    /* color: black; */
    color: #2e2727
        /* margin-top: 10px; */
}

.author_name {
    font-size: 15px;
    color: gray;
}

.free_price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    width: 100px;
}

.free_ {
    font-size: 16px;
    color: green;
    margin-right: 5px;
}

.price {
    font-size: 16px;
    color: red;
}

.books_container {
    display: flex;

}


._heading {
    margin: 30px 0px 22px 16px;
    font-size: 23px;
    color: #881761;
    font-weight: 700;
    font-size: 22px;
    text-transform: uppercase;
}

.books_container {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.section_c,
.section_d {
    width: 100%;
    height: auto;
}

.container_c,
.container_d {
    height: auto;
    width: 80vw;
    margin: 12px auto 40px auto;
}

.contianer_d {
    margin-bottom: 64px;
}

footer {
    width: 100%;
    height: auto;
    color: white;
    background-color: rgb(131, 28, 105);
    padding: 25px 15px;
}


.block-footer h5 {
    margin: 15px;
    font-size: 18px;
}

.expanded_ul_item span {
    font-size: 19px;
    font-weight: bold;
    color: #e021afe8;
}

.expanded_ul_item a {
    color: white;
    text-decoration: none;
}

.expanded_ul {
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.expanded_ul_item {
    margin: 0px 37px;
    padding: 3px 10px;
}

.footer_dd {
    margin-top: 5px;
}

.footer_dd li a {
    text-decoration: none;
    color: white;
    font-weight: 2000;
}

.footer_content {
    display: flex;
    justify-content: space-around;
    align-content: center;
    margin-top: 22px;
}

.footer_cntn span img {
    height: 18px;
    width: 21px;
    margin-right: 7px;
    position: relative;
    top: 3px;
}

.div_line {
    width: 74vw;
    margin: 12px auto 0px auto;
}

.line {
    border-top: 2.5px solid #ad3c91e8;
}


.footer_content {
    font-size: 16px;
    color: #e021afe8;
}

.footer_cntn p {
    display: inline;
}

.hamburger {
    display: none;
}

.login-account-ul {
    display: none;
}

#login-profile {
    display: none;
}

#down-arrow-span {
    width: 20px;
    display: inline-block;
}


@media (max-width: 768px) {
    .nav {
        flex-direction: column;
        align-items: flex-start;
        padding: 21px 20px 23px;
        height: 17vh;
        height: auto;
    }

    .search-box {
        order: 3;
        width: 100%;
        position: relative;
    }

    .account-info {
        align-self: flex-end;

    }

    .hamburger {
        display: block;
        cursor: pointer;
        font-size: 19px;
        color: white;
        align-self: flex-end;
        position: relative;
        bottom: 22px;
    }

    .login-signup-bttns {
        order: 2;
        margin-top: 10px;
    }

    .nav .login-signup-bttns .bttn {
        appearance: none;
        background: none;
        border: none;
        padding: 0;
        margin: 0;
        font-size: inherit;
        font-family: inherit;
        cursor: pointer;
        text-decoration: none !important;
        color: #534e4e;
        font-weight: normal;
    }

    .nav .login-signup-bttns .bttn:hover {
        text-decoration: none;
        box-shadow: none;
        border: none;
        background: none;
        transform: none;
    }

    .user-logo {
        display: none;
    }


    .login-signup-bttns {
        display: flex;
        flex-direction: column;
        width: auto;
        margin-bottom: 6px;
    }

    .login-account-ul {
        display: block;
    }


    .account-content {
        background: none;
        position: relative;
        top: 0px;
        height: auto;
        padding: 0px;
        box-shadow: none;
        display: block;
    }

    .account-info {
        display: none;
        width: auto;
        background-color: #f3f3f3;
        position: absolute;
        top: 63px;
        height: auto;
        padding: 2px 7px;
        box-sizing: border-box;
        box-shadow: 0px 0px 7px 3px #8c8c8c;
        z-index: 3;
        right: 37px;
        flex-direction: column-reverse;
    }

    #bttn1 {
        display: none;
    }

    .nav .search-box input {
        width: 100%;
    }

    #logo-search {
        right: 23px;
        position: absolute;
        top: 8px;
    }

    .block-content {
        /* display: none; */
        z-index: 1;
    }

    .expanded_ul {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }

    .block-footer {
        width: 100%;
    }

    .footer_dd {
        margin-top: 5px;
        display: none;
    }

    .free_books .cover {
        margin: 29px 9px;
        width: 121px;
        height: 249px;
    }

    .container_b {
        width: 95vw;
        margin: 12px auto 50px auto;
    }

    .sub-contianer-editor-choice {
        width: 95vw;
    }

    .cover {
        margin: 22px 12px;
        width: 107px;
        height: 187px;
    }

    .book_name {
        font-size: 15px;
    }

    .container_c,
    .container_d {
        height: auto;
        width: 95vw;
    }

    .sub-contianer-editor-choice {
        height: auto;
        width: 95vw;
    }

    #down-arrow-user {
        right: 7px;
        font-size: 21px;

    }

    .hamburger #down-arrow-span-user {
        position: absolute;
        right: 64px;
    }

    .free_books .cover {
        margin: 29px 9px;
        width: 115px;
        height: 249px;
    }

    #down-arrow-user {
        /* right: 33px; */
        font-size: 21px;
        position: relative;
        top: 2px;
        left: -5px;
    }

    .account-info {
        display: none;
    }

    .account-content {
        display: flex;
    }

    #login-profile {
        display: block;
    }

    .visible {
        display: block;
    }

    .expanded_ul_item span {
        font-size: 19px;
        font-weight: bold;
        color: #e021afe8;
        position: relative;
    }


    .expanded_ul_item span::after {
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: rgb(212 210 211);
        bottom: 1px;
        transition: transform 0.3s ease;
        transform: scaleX(0);
        transform-origin: left;
    }


    .expanded_ul_item span:hover::after {
        transform: scaleX(1);
        color: white;
    }

}



@media (max-width: 280px) {

    .nav-bg-image {
        height: 385px;
    }

    .container_a .genre_heading {
        margin: 22px 0px 22px 7px;
        font-size: 17px;
    }

    .genre_items .item {
        margin: 10px 0px;
        width: 192px;
        height: 159px;
    }

    .genre_items .item .text {
        position: absolute;
        top: 59px;
    }

    .container_a {
        margin: 12px auto 40px auto
    }

    .container_c,
    .container_d {
        height: auto;
        margin: 12px auto 30px auto;
        width: 95vw;
    }

    .free_books .cover {
        margin: 12px 12px;
        width: 113px;
        height: 207px;
    }

    .free_books .cover {
        margin: 12px 12px;
        width: auto;
        height: auto;
    }

    .container_b .cover img {
        width: 100%;
        height: 72%;
        width: 98px;
        height: 140.2px;
        cursor: pointer;
    }

    .block-content {

        z-index: 1;

    }

    .block-content {
        top: 37px;
        padding: 28px 31px;
        width: auto;
        height: auto;
    }

    .genres-list ul {
        list-style: none;
        display: grid;
        grid-template-columns: none;
        grid-gap: 10px;
    }

    .genres-list ul li {
        font-size: 15px;
    }
}

@media (max-width: 550px) {
    .block-content {
        width: 300px;
        background-color: #f3f3f3;
        position: absolute;
        top: 37px;
        height: auto;
        padding: 28px;
        box-sizing: border-box;
        box-shadow: 0px 0px 7px 3px #8c8c8c;
        padding-bottom: 37px;
    }

    .genres-list ul li {
        font-size: 15px;
    }
}

@media (max-width: 380px) {
    .block-content {
        width: auto;
        background-color: #f3f3f3;
        position: absolute;
        top: 37px;
        height: auto;
        padding: 28px;
        box-sizing: border-box;
        box-shadow: 0px 0px 7px 3px #8c8c8c;
        padding-bottom: 37px;
    }

    .genres-list ul li {
        font-size: 15px;
    }

    .genres-list ul {
        list-style: none;
        display: grid;
